#toTop {
  position: fixed;
  right: 20px;
  bottom: 20px;
  font-size: 0;
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  color: #fff;
  background: var(--brand-primary);
  border-radius: 50%;
  overflow: hidden;
  text-decoration: none;
  z-index: 1200;
  visibility: visible;
  opacity: 1;
  transform: scale(1);
  transition: 0.3s all ease;
}

#toTop:before {
  font-family: "Font Awesome\ 5 Free";
  content: '\f106';
  font-weight: 900;
  font-size: 26px;
}

#toTop:hover, #toTop:focus {
  color: #fff;
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
  text-decoration: none;
}

@media (min-width: 576px) {
  #toTop {
    right: 34px;
    bottom: 34px;
  }
}

