.brk-svg-pattern-container {
  position: absolute;
  width: 100%;
  overflow: hidden;
  z-index: 3;
}

.brk-svg-pattern-container svg {
  position: absolute;
  width: calc(100% + 2px);
}

.brk-svg-pattern-container_top {
  top: 0;
}

.brk-svg-pattern-container_top svg {
  top: -5px;
}

.brk-svg-pattern-container_bottom {
  bottom: 0;
}

.brk-svg-pattern-container_bottom svg {
  bottom: -5px;
}

.img-icon {
  max-width: 100%;
  max-height: 100%;
  width: auto;
  height: auto;
}

.brk-wave-line {
  display: block;
  width: 100%;
  height: 7px;
  background-image: url("../../img/wave-line.png");
  background-repeat: repeat-x;
  background-color: transparent;
  border: 0;
}

.brk-wave-line-blue {
  display: block;
  width: 100%;
  height: 7px;
  background-image: url("../../img/wave-line-2.png");
  background-repeat: repeat-x;
  background-color: transparent;
  border: 0;
}

.centering-child-img img,
.centering-img {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

/*shapes*/
.full__size-absolute {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: block;
}

.brk-rating {
  position: relative;
  width: 89px;
}

.brk-rating__layer:before, .brk-rating__layer:after {
  content: " ";
  display: table;
}

.brk-rating__layer:after {
  clear: both;
}

.brk-rating__layer i {
  display: inline-block;
  float: left;
  font-size: 0.875rem;
  margin: 0 1px;
}

.brk-rating__imposition {
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
}

.brk-rating__imposition:before, .brk-rating__imposition:after {
  content: " ";
  display: table;
}

.brk-rating__imposition:after {
  clear: both;
}

.brk-rating__imposition .visible {
  width: 89px;
  height: 15px;
}

.brk-rating__imposition i {
  display: inline-block;
  float: left;
  font-size: 0.875rem;
  margin: 0 1px;
}

.center-absolute {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.brk-lightning-effect {
  position: relative;
  overflow: hidden;
  display: inline-block;
}

.brk-lightning-effect:before {
  content: '';
  width: 50px;
  position: absolute;
  top: 0;
  left: -50px;
  bottom: 0;
  z-index: 9;
  background: linear-gradient(to right, rgba(255, 255, 255, 0.2) 0%, white 50%, rgba(255, 255, 255, 0.2) 100%);
}

.brk-lightning-effect:hover:before {
  animation: lightning-effect 2s infinite ease-in-out;
}

@keyframes lightning-effect {
  0% {
    left: -50px;
    background: linear-gradient(to right, rgba(255, 255, 255, 0.2) 0%, white 50%, rgba(255, 255, 255, 0.2) 100%);
  }
  50% {
    left: 100%;
  }
  100% {
    left: -50px;
    background: transparent;
  }
}

.brk-bg-center-cover {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.brk-bg-center-cover .container {
  z-index: 2;
  position: relative;
}

.brk-bg-right-center {
  background-position: right center;
  background-repeat: no-repeat;
  background-size: contain;
}

.brk-bg-left-center {
  background-position: left center;
  background-repeat: no-repeat;
  background-size: contain;
}

