/* hexagon */
.hexagon {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 320px;
  margin: 0 auto;
  background: #fff;
  padding: 30px 30px;
  clip-path: polygon(80% 0, 100% 50%, 80% 100%, 20% 100%, 0 50%, 20% 0);
  position: relative;
  z-index: 1;
}

@media (min-width: 480px) {
  .hexagon {
    width: 430px;
    height: 380px;
    padding: 30px 70px;
    clip-path: polygon(75% 0, 100% 50%, 75% 100%, 25% 100%, 0 50%, 25% 0);
  }
}

@media (min-width: 1200px) {
  .hexagon {
    margin: 0 auto;
  }
}

@media (min-width: 992px) {
  .hexagon__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 1200px) {
  .hexagon__wrap {
    margin: -180px auto;
  }
  .hexagon__wrap .hexagon {
    margin: 0;
  }
}

.hexagon > .after {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  background: var(--brand-primary);
  background: linear-gradient(to left, var(--brand-primary), var(--secondary));
  clip-path: polygon(80% 0, 100% 50%, 80% 100%, 20% 100%, 0 50%, 20% 0);
  opacity: 0;
  transform: scale(1);
  will-change: transform;
  transition: all .4s ease;
  z-index: -1;
}

@media (min-width: 480px) {
  .hexagon > .after {
    clip-path: polygon(75% 0, 100% 50%, 75% 100%, 25% 100%, 0 50%, 25% 0);
  }
}

.hexagon .highlight-massive > .after {
  height: 3px;
  top: 98%;
}

.hexagon .btn {
  background: transparent;
  color: #fff;
  opacity: 0;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  margin-right: 0;
  margin-left: 0;
}

.hexagon .btn > .before, .hexagon .btn > .after {
  background: #fff;
}

.hexagon .btn .border-btn {
  border-color: #fff;
}

.hexagon .btn:hover {
  color: #333 !important;
}

.hexagon .cover {
  position: relative;
  transition: all .4s ease;
}

